@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
.Navbar {
    background-color: var(--background-p);
    position: fixed;
    opacity: 0.99;
    width: 100%;
    height: 64px;
}

.NavContent {
    display: flex;
    width: 100%;
    height: 64px;
    padding-left: 6%;
    padding-right: 6%;
    justify-content: space-between;
    align-items: center;
}

.ProjectName p {
    color: var(--text-light-p);
    font-size: 1.4rem;
    margin: 0;
}

.NavLink {
    margin-right: 65px;
}

.LastNavLink {
    margin-right: 0 !important;
}

@media (max-width: 1250px) {
    .NavLink {
        display: none !important;
    }
}
@media (max-width: 1000px) {
    .NavContent {
        padding-left: 5%;
        padding-right: 5%;
    }
}
