.Homepage {
    background-color: var(--background-p);
    font-family: var(--font);
    color: var(--text-light-p);
    font-size: 1rem;
    width: 100%;
    height: 100vh;
}

.MainBody {
    padding-top: 64px;
}

.ProjectInfo {
    text-align: center;
}

.ProjectInfo h2 {
    font-size: 4.75rem;
    line-height: 75px;
    font-weight: 600;
    margin-top: 7%;
}

.ProjectInfo h4 {
    color: var(--text-light-s);
    font-size: 1.3rem;
    line-height: 30px;
    margin-top: 2%;
}

.Signup {
    background-color: var(--background-p);
    font-family: var(--font);
    color: var(--text-s);
    font-size: 1rem;
    width: 100%;
    height: 100vh;
}

.Card {
    width: 500px;
    height: 650px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}


/* DEFAULT MARGIN AND PADDING */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* ROOT VARIABLES FOR STYLE */
:root {
    --accent: #c38fff;
    --background-p: #202020;
    --background-s: #000000;
    --text-light-p: #f2f2f2;
    --text-light-s: #8f8f8f;
    --font: 'Poppins', sans-serif;
}

/* DEFAULT SCROLLBAR */
::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-thumb {
    background: #c38fff;
    background: var(--accent);
}
::-webkit-scrollbar-track {
    background: #202020;
    background: var(--background-p);
}

/* DEFAULT STYLING */
html {
    background-color: #202020;
    background-color: var(--background-p);
    font-family: 'Poppins', sans-serif;
    font-family: var(--font);
    color: #f2f2f2;
    color: var(--text-light-p);
}

